"use client";
import { useState } from "react";

export default function CreateTournamentPage() {
  const [name, setName] = useState("");
  const [system, setSystem] = useState("round-robin");
  const [time, setTime] = useState(10);
  const [date, setDate] = useState("");
  const [lockSystem, setLockSystem] = useState(false);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    alert(`Torneo creado: ${name}\nSistema: ${system}\nTiempo: ${time} min\nFecha: ${date}`);
    // Guardar torneo en state global o DB
  };

  const today = new Date().toISOString().split("T")[0]; // YYYY-MM-DD

  return (
    <div style={{ maxWidth: "500px", margin: "3rem auto", padding: "2rem", backgroundColor: "#fff", borderRadius: "10px", boxShadow: "0 0 10px rgba(0,0,0,0.1)" }}>
      <h2 style={{ color: "#4f46e5" }}>Crear Torneo</h2>
      <form onSubmit={handleSubmit} style={{ display: "flex", flexDirection: "column", gap: "1rem", marginTop: "1rem" }}>
        <input
          type="text"
          placeholder="Nombre del torneo"
          value={name}
          onChange={(e) => setName(e.target.value)}
          required
        />
        <select value={system} onChange={(e) => setSystem(e.target.value)} disabled={lockSystem}>
          <option value="round-robin">Round-robin</option>
          <option value="single-elimination">Eliminaci√≥n simple</option>
          <option value="swiss">Suizo</option>
        </select>
        <select value={time} onChange={(e) => setTime(Number(e.target.value))}>
          <option value={5}>5 minutos</option>
          <option value={10}>10 minutos</option>
          <option value={15}>15 minutos</option>
        </select>
        <input
          type="date"
          value={date}
          min={today}
          onChange={(e) => setDate(e.target.value)}
          required
        />
        <label style={{ display: "flex", alignItems: "center", gap: "0.5rem" }}>
          <input
            type="checkbox"
            checked={lockSystem}
            onChange={(e) => setLockSystem(e.target.checked)}
          />
          Solo emparejamiento (bloquea otros sistemas)
        </label>
        <button type="submit" style={{ padding: "0.5rem", backgroundColor: "#4f46e5", color: "#fff", border: "none", borderRadius: "5px" }}>
          Crear Torneo
        </button>
      </form>
    </div>
  );
}
